@import '../variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    display: flex;
    align-items: center;

    img {
      margin-right: 15px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;

      @media (max-width: 550px) {
        font-size: 18px;
      }

      @media (max-width: 490px) {
        display: none;
      }
    }

    p {
      color: #7b7b7b;

      @media (max-width: 600px) {
        display: none;
      }
    }
  }

  &__cart {
    display: flex;
    justify-content: flex-end;
    width: 188px;
  }
}
